// import Bmob from '@/utils/bmob.js'
import postCard from '@/components/postCard';
export default {
	components: {
		postCard
	},
	data() {
		return {
			commentValue: '',	// 评论内容
			postId: '',	// 帖子id
			postInfo: {},	// 帖子信息
			commentsList: [],	// 评论列表
		}
	},
	computed: {
	},
	async onLoad(option) {
		this.postId = option.postId ? option.postId : '8604d6bb67';
		this.getPost();
		this.getComments();
	},
	onReady(){
	},
	// 分享
	onShareAppMessage() {
		return {
			title: '信息分享',
			path: '/pages/post/postDetail?postId=' + this.postId
		}
	},
	// 到底
	onReachBottom(){
		// if (this.pageSetting.pageIndex <= this.pageSetting.totalPage) {
		// 	//设置列表数据
		// 	this.getPost()
		// }
	},
	methods: {
		// 获取帖子列表
		getPost() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			const query = that.Bmob.Query('postList');
			query.include("author", "_user");
			query.get(that.postId).then(res => {
				uni.hideLoading();
				that.postInfo = res;
			    console.log(res)
			}).catch(err => {
			  console.log(err)
			})
		},
		// 获取评论列表
		getComments () {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			const query = that.Bmob.Query("comment");
			query.equalTo("attrPost","==", that.postId);
			query.include("own", "_user");
			query.find().then(res => {
				uni.hideLoading();
			    that.commentsList = res;
			});
		}
	}
}
